// src/store/modules/session/slice.ts
import { createSlice, PayloadAction } from '@reduxjs/toolkit';

interface SessionState {
  valid: boolean;
  loading: boolean;
  userId: string | null;
  token: string | null;
  chatId: string | null;
}

const initialState: SessionState = {
  valid: false,
  loading: false,
  userId: null,
  token: null,
  chatId: null,
};

const sessionSlice = createSlice({
  name: 'session',
  initialState,
  reducers: {
    validateSessionRequest: (state) => {
      console.log('üîÑ Iniciando valida√ß√£o da sess√£o...');
      state.loading = true;
    },
    validateSessionSuccess: (state, action: PayloadAction<any>) => {
      console.log('‚úÖ Sess√£o validada com sucesso:', action.payload);
      state.loading = false;
      state.valid = true;
      
      if (action.payload.userId) {
        state.userId = action.payload.userId;
      }

      if (action.payload.token) {
        state.token = action.payload.token;
      }
    },
    validateSessionFailure: (state) => {
      console.log('‚ùå Falha na valida√ß√£o da sess√£o');
      state.loading = false;
      state.valid = false;
    },
  },
});

export const {
  validateSessionRequest,
  validateSessionSuccess,
  validateSessionFailure,
} = sessionSlice.actions;

export default sessionSlice.reducer;
