'use client';

/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable @typescript-eslint/no-explicit-any */

// src/store/modules/session/saga.ts
import { ApiService } from '@/services/apiSercice';
import { RootState } from '@/store';
import { call, put, select, takeLatest } from 'redux-saga/effects';
import {
  profileFailure,
  profileRequest,
  profileSuccess
} from './slice';

const handleprofile = function* ({ payload }: any): any {
  let token = payload.token;
  if (!token) {
    token = yield select((state: RootState) => state.session.token);
  }

  try {
    console.log('üîÑ preparando para pegar o perfil');
    console.log('üîÑ token:', token);

    const api = new ApiService(token);
    const response = yield call([api, api.getUserProfile]);
    const data = response?.data;
    console.log('üîÑ Resposta da sess√£o:', data);

    yield put(profileSuccess({
      chatId: data?.chatId,
      name: data?.name,
      valid: true,
    }));
    return;
  } catch (err: any) {
    console.error('üí• Erro ao validar sess√£o:', err);
    yield put(profileFailure());
    return;
  }
}


export default function* sessionSaga() {
  console.log('üöÄ Inicializando saga de sess√£o');
  yield takeLatest<any>(
    profileRequest.type,
    handleprofile
  );
}
