// src/store/modules/session/slice.ts
import { CoreMessageResponse } from '@/interface/chats';
import { createSlice, PayloadAction } from '@reduxjs/toolkit';

interface SessionState {
  valid: boolean;
  loading: boolean;
  response: CoreMessageResponse | null;
}

const initialState: SessionState = {
  valid: false,
  loading: false,
  response: null,
};

const sessionSlice = createSlice({
  name: 'sendMessage',
  initialState,
  reducers: {
    sendMessageRequest: (state, action: PayloadAction<{ chatId: string, message: string }>) => {
      console.log('üîÑ Iniciando valida√ß√£o da sess√£o...');
      state.loading = true;
    },
    sendMessageSuccess: (state, action: PayloadAction<any>) => {
      console.log('‚úÖ Sess√£o validada com sucesso:', action.payload);
      state.loading = false;
      state.valid = true;
      state.response = action.payload.response;
    },
    sendMessageFailure: (state) => {
      console.log('‚ùå Falha na valida√ß√£o da sess√£o');
      state.loading = false;
      state.valid = false;
    },
  },
});

export const {
  sendMessageRequest,
  sendMessageSuccess,
  sendMessageFailure,
} = sessionSlice.actions;

export default sessionSlice.reducer;
